*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.homeBody{
    width: 100%;
    height: 100%;
}
.brands{
    width: 100%;
    height: 100%;
}
.brands h1{
    font-size: 10vh;
    margin: 6vh 25%;
    border-bottom: 3px groove #666;
    text-align: center;
    color: black;
}
.brands h4{
    font-size: 4vh;
}
.brands{
    display: flex;
    flex-direction: column;
}
.brands select{
    width: 15vw;
    text-align: center;
    margin: 3vh;
    padding: 1vh;
    font-size: 3vh;
    border-radius: 5%;
    border: none;
    outline: none;
    border-bottom:4px solid #b7b7b7;
    background-color: #b7b7b7;
}
.brandsHolder{
    display: grid;
    grid-template-columns: repeat(3, 0fr);
    justify-content: space-around;
    padding: 1vh 3vh;
    letter-spacing: 0.5vh;
}
.categs{
    width: 30vw;
    max-height: 40vh;
    border-radius: 2vh;
    margin: 3vh 0;
    background-color: #fff;
    box-shadow: 0 1vh 3vh rgba(0,0,0,0.12), 0 1vh 2vh rgba(0,0,0,0.24);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
    text-align: center;
}
.categs h3{
    font-size: 3rem;
    border-bottom: 3px groove #f69d00;
    font-weight: 500;
    display: inline-flex;
    align-items: center;
}
.categs h3 img{
    width: 1.2vw;
    height: 4vh;
    transform: scale(1.5);
    align-self: center;
    margin-left: 1vw;
}
/* .categs img{
    width: 25vw;
    height: 20vh;
    margin: 3vh 0;
    border-radius: 2vh;
    animation: brands 3.51s ease-in infinite;
} */
.logos{
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(3, 0fr);
    justify-content: center;
    align-items: center;
    padding: 2vh 0vw;
}
.logos img{
    width: 4vw;
    height: 5vh;
    margin: 1vh 2vw;
}

@keyframes brands {
    0%{
        opacity: 0;;
    }
    
    50%{
        opacity: 0.75;
    }
    100%{
        opacity: 1;
    }
}
.miss-card{
    width: calc(100% - 20%);
    height: 100%;
    margin: 0 auto 10vh;
    box-shadow: 0 10px 50px rgba(0, 0, 0, 0.3);
    padding: 4vh;
    border-radius: 2vh;
    line-height: 6vh;
    letter-spacing: .1em;
    font-size: 1.3em;
}
    /* Mobile Styling */

.brands.mob h1{
    font-size: 2.5rem;
    margin: 2vh 5% ;
}
.brands.mob .brandsHolder{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 95%;
    margin: 5vh auto 10vh;
}
.brands.mob .categs{
    width: 90vw;
}
.brands.mob .categs h3{
    font-size: 2rem;
}
.brands.mob .categs h3 img{
    width: 5vw;
    height: 2vh;
}
.brands.mob .brandsHolder h2{
    font-size: 2em;
    margin-bottom: 5vh;
}
.brands.mob .logos img{
    width: 25vw;
    height: 5vh;
}
.brands.mob .miss-card{
    width: calc(100% - 5%);
    margin: 0 auto 10vh;
    padding: 2vh 1vh;
    line-height: 6vh;
    letter-spacing: 0;
    font-size: 1em;
}
