.screen.log{
    height: 45vh;
}
.log_container {
    flex: 0 1 35vw;
    margin: auto;
    padding: 2vh;
}
.log_screen{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 80%;
}
.log_screen label{
    display: flex;
    flex-direction: column;
    font-size: 1.2rem;
    color: #fff;
}
.log_screen label input{
    margin: 2vh 0;
}
.log_screen button{
    width: 7vw;
    height: 5vh;
    border-radius: 2vh;
    cursor: pointer;
    border: none;
    outline: none;
    background-color: #f69d00;
    font-size: 1.2rem;
}

.log_container.mob{
    flex: 0 1 85vw;
}
.log_screen.mob button{
    width: fit-content;
    padding: 0 1vh;
}