footer{
	background: #222;
	padding: 40px 0;
	display: flex;
	align-items: center;
}
footer .footer-cont{
	width: 100%;
	max-width: 1100px;
	margin: 0 auto;
	display: flex;
	align-items: flex-start;
	justify-content: space-between;
	flex-wrap: wrap;
}
footer .ftr h3{
	font-size: 1.4em;
	margin-bottom: 1.2em;
	color: #fff;
}
footer .ft-content{
	list-style: none;
}
footer .ft-content a{
	text-decoration: none;
	color: #fff;
	opacity: .6;
	line-height: 40px;
	transition: .3s ease opacity;
}
.ft-content i{
	margin: 0 2vh 0 0; 
	font-size: 2.5vh;
}
footer .ft-content a:hover{
	opacity: 1;
}
footer .ftr{
	flex-basis: 20%;
}
.ftr form{
	display: flex;
	align-items: center;
	width: 100%;
}
.ftr input{
	border: none;
	outline: none;
	padding: 10px;
	margin: 5px 0;
	background: #f5f5f5;
}
.news-btn input{
	background: #f69d00;
	color: #fff;
	transition: .3s ease background ;
}
.news-btn input:hover{
	background: #000;
}
.ftr-logo{
	display: flex;
	align-items: center;
	flex-direction: column;
	width: 20vw;
	height: 15vh;
	color: #f69d00;
}
.ftr-logo img{
	width: 100%;
	height: 100%;
}
/* ------ */
.lang_menu.mobile{
    position: relative;
}
.selected_lang{
    cursor: pointer;
    display: flex;
    justify-content: space-between;
	align-items: center;
    line-height: 2;
    width: 14vh;
    height: 3vh;
    font-size: 2vh;
	padding: 2vh 0%;
	margin: 2vh auto;
	color: #fff;
}
.selected_lang img{
    width: 3.2vh;
    height: 3.2vh;
}
.lang_menu ul{
    background: #fff;
    border: 1px solid #f8f8f8;
    box-shadow: 0 1px 10px rgba(0,0,0,0.2);
    border-radius: 5px;
    position: absolute;
    top: 4vh;
    left: 28%;
    z-index: 100;
    overflow: hidden;
    height: 0;
    opacity: 0;
    transition: opacity 300ms 0ms;
}
.lang_menu ul li {
    list-style: none;
    display: flex;
    justify-content: space-between;
}
.lang_menu ul li a{
    width: 15vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1vh;
    color: #000;
    cursor: pointer;
    font-size: 1.5vh;
	user-select: none;
}
.lang_menu ul li a img{
    width: 3.2vh;
    height: 3.2vh;
}
.lang_menu.mobile ul li a img{
    width: 3.2vh;
    height: 3.2vh;
}
.lang_menu ul li a:hover{
    background: radial-gradient(circle at 94.02% 88.03%, #c2a970, transparent 100%);
}

.lang_menu.act ul {
    height: auto;
    opacity: 1;
    transition: opacity 500ms 0ms;
}
.footer_body.mobile{
    bottom: 0;
    height: 30vh;
    padding-top: 0;
}
.footer-content.mobile{
    flex-direction: column;
    justify-content: space-between;
    height: 30vh;
    background-color: #222;
    padding: 1vh 0;
}
.footer-bottom.mobile{
    position: absolute;
    bottom: 0;
}
.socials.mobile li a img{
    width: 3vh;
    height: 3vh;
}
.selected_lang.mobile{
    justify-content: space-evenly;
}
.selected_lang.mobile img{
    width: 3.2vh;
    height: 3.2vh;
}

/* Mobile Styling */
.mob_footer{
	
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
}
.ftr-logo.mob{
	width: 100%;
	height: 10vh;
	margin-bottom: 10vh;
}
@media only screen and (max-width: 540px){
	footer .ftr{
		flex-basis: 80%;
		margin: 0 auto;
	}
	.ftr form{
		flex-direction: column;
	}
	.ftr input{
		width: 100%;
		margin: 5px auto;
		
	}
}